import axios from 'axios';
import SlimSelect from 'slim-select';
// Ustawienie nagwka z kluczem API
axios.defaults.headers.common['x-api-key'] =
  'live_Azme2SGnu6hL9Sr8t6i2ko7AHTHcmkv2YFx2dLSlUrOohIiXyuSbZh3xe00pBDOH';

// Powizanie elementw DOM
const breedSelect = document.querySelector('.breed-select');
const catInfoContainer = document.querySelector('.cat-info');
const loader = document.querySelector('.loader');
const error = document.querySelector('.error');

// Dodanie nasuchiwania na zdarzenie zmiany dla select
breedSelect.addEventListener('change', handleBreedChange);

// Funkcja obsugujca zmian rasy kota
function handleBreedChange(event) {
  const breedId = event.target.value;
  if (breedId) {
    showLoader();
    hideError();
    fetchCatInfo(breedId);
  } else {
    hideCatInfo();
  }
}

// Funkcja wywietlajca animacj adowania
function showLoader() {
  loader.style.display = 'block';
}

// Funkcja ukrywajca animacj adowania
function hideLoader() {
  loader.style.display = 'none';
}
// Funkcja wywietlajca komunikat o bdzie
function showError() {
  error.style.display = 'block';
}
// Funkcja ukrywajca komunikat o bdzie
function hideError() {
  error.style.display = 'none';
}

// Funkcja wykonujca danie informacji o kocie
function fetchCatInfo(breedId) {
  // Wykonaj danie HTTP, aby pobra informacje o kocie na podstawie identyfikatora rasy
  axios
    .get(`https://api.thecatapi.com/v1/images/search?breed_ids=${breedId}`)
    .then(response => {
      //console.log(response.data[0]);
      const catInfo = response.data[0];
      showCatInfo(catInfo);
    })
    .catch(error => {
      console.error(error);
      showError();
    })
    .finally(() => {
      hideLoader();
    });
}

// Funkcja wywietlajca informacje o kocie
function showCatInfo(catInfo) {
  const { name, description, temperament } = catInfo.breeds[0];
  const { url } = catInfo;
  const catInfoHTML = `
    <div>
    <img  src="${url}" alt="">
      <h2>${name}</h2>
      <p><strong>Description:</strong> ${description}</p>
      <p><strong>Temperament:</strong> ${temperament}</p>
    </div>
  `;

  catInfoContainer.innerHTML = catInfoHTML;
  catInfoContainer.style.display = 'block';
}

// Inicjalizacja aplikacji - pobranie listy ras kota
function initializeApp() {
  showLoader();
  axios
    .get('https://api.thecatapi.com/v1/breeds')
    .then(response => {
      const breeds = response.data;
      fillBreedSelect(breeds);
    })
    .catch(error => {
      console.error(error);
      showError();
    })
    .finally(() => {
      hideLoader();
    });
}

// Funkcja wypeniajca select opcjami ras kotw
function fillBreedSelect(breeds) {
  breeds.forEach(breed => {
    const option = document.createElement('option');
    option.value = breed.id;
    option.textContent = breed.name;
    breedSelect.appendChild(option);
  });
}

// Inicjalizacja aplikacji po zaadowaniu strony
document.addEventListener('DOMContentLoaded', () => {
  initializeApp();
});
